___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --header-height: 65px;
  --hero-height: calc(100% - (var(--header-height) + 45%));
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  height: 100%;
}

body,
header {
  background-color: rgba(215, 155, 130, 0.55);
}

header {
  box-shadow: 1px 1px 6px rgb(0, 0, 0, 0.25);
  height: var(--header-height);
  position: relative;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;
}

nav > ul {
  list-style: none;

  display: flex;
  gap: 1rem;
}

button {
  cursor: pointer;
  padding: 0.5rem 1.5rem;
}

.hero-container {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center 85%;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
  height: var(--hero-height);
  padding: 0.85rem;
  

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-content {
  background-color: rgba(40, 40, 40, 0.75);
  border-radius: 0.30rem;
  color: rgb(255, 255, 255);
  margin: 0 auto;
  max-width: 80ch;
  text-align: center;
  padding: 0.75rem;
}

.hero-content > p {
  font-size: 1.20rem;
  line-height: 1.5;
}

.content {
  height: var(--content-height);
  padding: 1rem;
  
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.content > div {
  padding: 0.5rem;

  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.content img {
  aspect-ratio: 1;
  border-radius: 0.35rem;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.35);
  max-width: 200px;
  object-fit: cover;
  width: 100%;
}

.content a {
  color: rgb(37, 37, 37);
}

.content a + p {
  line-height: 0;
}

.content p {
  font-size: 0.88rem;
}

.contact-section {
  font-size: 1.25rem;
}

.contact-section p {
  font-size: 1.05rem;
}

.contact-info h3 {
  margin-top: 0.75rem;
}

.contact-info h3:first-child {
  margin: 0;
}
